import React from 'react'
import './style.css'
import iabw from './assets/iabw.svg'
import iacolor from './assets/iacolor.svg'
import frontbw from './assets/frontbw.svg'
import frontcolor from './assets/frontcolor.svg'


export default function Servicos(){

    return(
        <div className="container">
            <div className="titulo">
                <h2>MEUS SERVIÇOS</h2>
            </div>
            <div className="div-class-servico">
                <div className="class-servico">
                    <div className="servico-1">
                        <div className="imagem">
                            <img src={frontbw} id="imgbw"></img>
                            <span><img src={frontcolor}></img></span>
                        </div>
                        <div className="texto">Desenvolvedor Front-End. 
                            <p> </p>
                        </div>
                    </div>
                    <div className="servico-2">
                        <div className="imagem">
                            <img src={iabw} id="imgbw"></img>
                            <span><img src={iacolor}></img></span>
                        </div>
                        <div className="texto">Cientista de Dados</div>
                    </div>
                </div>
            </div>
        </div>
    )
}